
body {
  text-align: center;
  /*background-color: #fff;*/

  background-color:#D2D2D2;
  font-family: -apple-system, BlinkMacSystemFont,
    “Segoe UI”, “Roboto”, “Oxygen”, “Ubuntu”, “Cantarell”,
    “Fira Sans”, “Droid Sans”,
    “Helvetica Neue”, 
    sans-serif;
}

 .wrapper {
  display: grid;
  grid-template-rows:20% 35% 35% auto;
  /*align-content: space-around;*/
  grid-gap: 10px;
  color: #444;
} 

.opis {
  font-size: 1.5em;
  font-weight:bold;
}

.tytul {
  font-size: 1.5em;
  font-weight: 900;
  text-transform: uppercase;
}

.item{
  padding:  10px;
  /*border: 2px solid black;*/
  background-color: #fff;
}

.input  {
  margin:  10px;
  color: #444;
  background: transparent;
  border: 3px dotted #999;
  border-radius: 5px;
  font-size:2em;
  text-align: center;
  width: 50%;
}

.output{
  color: #444;
  padding: 10px;
  background: transparent;
  /*border: 2px solid #999;*/
  border-radius: 5px;
  font-size:2.0em;
  font-weight: 900;
  text-align: center;

}


